.header {
  padding: 18px 0;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: $color-blue-whale;
  z-index: 3;
  background-color: $color-default-white;
  border-bottom: 1px solid #EAE9E8;
  min-width: 320px;

  ul{
  list-style: none;
  }

  a{
    color: $color-blue-whale;
  }

.header-nav{

  &__list {
    display: flex;
    margin: 0;
    padding: 0;
    list-style: none;

    a{
      @include hover {
        color: $color-submarine;
        transition: color $trans-default;
        }
      }

      li:first-child{
        margin-left: 0px;
      }

      li{
        margin-left: 40px;
      }

  }

  &__item-input{
    border: none;
    border-bottom: 2px solid #0F303F;
    background-color: transparent;
    color: inherit;
    outline: none;
    width: 150px;
  }


  &__item-form{
    display: flex;
    align-items: center;

    a{
      margin-right: 20px;
    }
  }

}

  .header-lang{

  &__list{
    display: flex;
  }

  &__item{
  margin-left: 12px;

  a{
    transition: color $trans-default;

  &:focus,
  &:active {
    text-decoration: underline;
    text-decoration-color: $color-cinnabar;
    text-underline-offset: 18px;
    text-decoration-thickness: 2px;
    color: $color-cinnabar;
    }
  }
}
}


.header-icons{

&__burger-icon{
  position: relative;
  width: 20px;
  height: 1px;
  margin-bottom: 7px;
  margin-top: 8px;
  background: $color-blue-whale;
  justify-content: center;
  display: none;

  &::after{
    content: ' ';
    position: absolute;
    width: 10px;
    height: 1px;
    background: $color-blue-whale;
    right: 0;
    top: 7px;
  }

}

  &__icon{
    margin-left: 12px;
    cursor: pointer;
    transition: transform $trans-default;

  &.is-active{
    transform: rotate(45deg);
    width: 20px;
    top: 0px;
    margin-bottom: 3px;
    padding-top: 2px;

    .header-icons__burger-icon{
    &::after{
      top: 0;
      width: 20px;
      transform: rotate(90deg);
    }
  }
  }
}

  &__list{

    display: flex;
    a{
      align-items: center;
    }

  li:last-child{
    margin-left: 22px;
  }
}
}
}


@media (max-width: 960px) {
  .header-nav{
    display: none;
  }

  .header-lang{
    display: none;
  }
  
  .header-icons__icon{
  .header-icons{

    &__burger-icon{
    display: block;
  }
}
}

}
